import Vue from 'vue'
import Router from 'vue-router'

// 首頁
import HomeRouter from './components/home'
// 貨幣換算
import ExchangeRouter from './components/exchange'
// 關於
import StatementRouter from './components/statement'
// 兌換
import ConverRouter from './components/convert'
Vue.use(Router)

export default new Router({
  scrollBehavior: () => ({
    x: 0,
    y: 0
  }),
  routes: [{
      path: '/',
      meta: { 
        requiresAuth: true,
        pagetitle : '首頁'
      },
      redirect: {
        name: 'Home',
        params: {
          language: 'tw'
        }
      }
    },
    ...HomeRouter,
    ...ExchangeRouter,
    ...StatementRouter,
    ...ConverRouter,
    {
      path: '*',
      meta: { 
        requiresAuth: true,
        pagetitle : '首頁'
      },
      redirect: {
        name: 'Home',
        params: {
          language: 'tw'
        }
      }
    }
  ],
})


