<template>
  <div id="app" v-if="$route.params.language">
    <!-- {{rates}} -->
    <Navbar-component @Setlang="Set_lang"></Navbar-component>
    <div class="container main-container my-30 p-20">
      <div class="main-container-logo">
        <img
          alt="Vue logo"
          src="./assets/logo1.png"
        >
      </div>
      <router-view :rates="rates" />
    </div>
    <FooterComponent/>
  </div>
</template>


<script>
  import NavbarComponent from "@/components/Navbar";
  import FooterComponent from "@/components/Footer";
  export default {
    name: "App",
    data: () => ({
      rates: []
    }),
    components: {
      NavbarComponent,
      FooterComponent
    },
    methods: {
      Set_lang(lang) {
        this.$i18n.locale = lang;
        this.$route.params.language = lang;
        this.$router.replace({ params: { language: lang } });
      }
    },
    watch: {
      $route(to) {
        let route = to.params.language;
         gtag('event', 'page_view', {
          'page':this.$route.name,
          'lang':this.$route.params.language,
        });
        if (route !== "tw" && route !== "en") {
          this.$router.replace({ params: { language: "tw" } });
        } else {
          this.$route.params.language = to.params.language;
          this.$i18n.locale = this.$route.params.language;
          this.$router.replace({ params: { language: route }, query:to.query });
        }
      }
    },
    mounted() {
      if(this.$route.name == 'home') return 
      this.axios
        .get("https://global-currency.herokuapp.com/currency")
        .then(({ data }) => {
          this.rates = data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    created() {
      let lang = this.$route.params.language;
      if (lang !== "tw" && lang !== "en") {
        this.$i18n.locale = "tw";
        lang = "tw";
      }
      if (lang === "tw") {
        this.$i18n.locale = "tw";
        lang = "tw";
      }
      if (lang === "en") {
        this.$i18n.locale = "en";
        lang = "en";
      }
    }
  };
 
</script>

<style lang="scss">
  @import "@/assets/scss/all.scss";
  body {
    background: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
  }
  #app {
    min-height: calc(100vh - 160px);
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    transition: color 0.2s;
  }
</style>
